import { useEffect,useState } from "react";

const Body=()=>{
    const key="5fcff7fd909a7f00e463bfd186b741f5";
    const base="https://api.openweathermap.org/data/2.5/";

    const [searchText,setSearhText]=useState("guntur")
    const [buttonClicked,setButtonClicked]=useState(true)
    const [weatherData,setWeatherData]=useState(null)
    const [error,setError]=useState(null);

    useEffect(()=>{
        if(buttonClicked){
            fetchData()
        }
    },[buttonClicked])

    async function fetchData()
    {
        const data=await fetch(`${base}weather?q=${searchText}&APPID=${key}`)
        const json=await data.json();
        if (json.cod === "404") {
            setError("City not found");
            setWeatherData(null);
        } else {
            setError(null); 
            setWeatherData(json);
        }
        setButtonClicked(false);
    }
   
    function handleButton()
    {
        setButtonClicked(true);
    }

    return(
        <>
            <div search-bar>
                <input 
                    className="search-input"
                    type="text" 
                    placeholder="search"
                    spellCheck="false"
                    value={searchText}
                    onChange={(e)=>{
                        setSearhText(e.target.value);
                    }}
                />
                <button className="search-button" onClick={handleButton}>Search </button>
            </div>
            {
               (error)?(<h1 className="error">{error}</h1>): (weatherData)?(
                    <>
                        <div className="rain-temp">
                            {/*<span className="city">{weatherData.name}</span>*/}
                            <span className="temp">{Math.round(weatherData.main.temp - 273.15)}°C</span>
                            <span className="rain">{weatherData.weather[0].description.toUpperCase()}⛈</span>
                        </div>
                        <div className="test">
                            <div>
                                <p>Temparature</p>
                                <h1>{Math.round(weatherData.main.temp - 273.15)}°C</h1>
                            </div>
                            <div>
                                <p>Humidity</p>
                                <h1>{weatherData.main.humidity}%</h1>
                            </div>
                            <div>
                                <p>Wind Speed</p>
                                <h1>{weatherData.wind.speed}</h1>
                            </div>
                        </div>
                    </>
                ):(
                    <h1 className="loading">LOADING..</h1>
                )
            }
        </>
    )
}

export default Body;